/**
 * ajax
 */
import axios from 'axios'
import { Message } from 'element-ui'

// axios
axios.defaults.timeout = 10000 // 
axios.defaults.baseURL = process.env.VUE_APP_API_URL // 
// axios.defaults.baseURL = 'http://cnbn801.boneng.hresysdms.cloud:18080/conn' // 
// axios.defaults.baseURL = 'http://a016.hresys.hresysdms.cloud:8082/conn' // 
// axios.defaults.headers.common['Language'] = val
// axios.defaults.baseURL = 'http://60.186.197.171:8081'
axios.interceptors.request.use(
  config => {
    let val = localStorage.langNum || 0
    let val1 = localStorage.AutoSystemID || 0
    config.headers['Language'] = val
    config.headers['AutoSystemID'] = val1
    return config
  }
)
// 
// http response 
axios.interceptors.response.use(
  response => {
    // eslint-disable-next-line eqeqeq
    if (response.data.code == 0) {
      // Message.success(response.data.msg)
      //  -1 ticket
      //      cookie.del("ticket")
      //      window.location.href='http://login.com'
      return response
    } else {
      // eslint-disable-next-line
      if (response.data.code == 2) {
        Message.error(response.data.msg)
        localStorage.removeItem('AutoSystemID')
        window.location.href = '/console/login'
      } else if (response.data.code === 3) {
        Message.warning(response.data.msg)
        return response
      } else if (response.data.code === 4) {
        Message.error(response.data.msg)
        setTimeout(() => {
          localStorage.removeItem('AutoSystemID')
          window.location.href = '/console/login'
        }, 1000)
      } else {
        // Message.error(response.data.msg)
        return response
      }
    }
  },
  error => {
    // if (error && error.response) {
    //   switch (error.response.status) {
    //     case 404:
    //       router.push({ name: 'errorPage' })
    //       // error.message = '(404)'
    //       break
    //     case 500:
    //       router.push({ name: 'error-500' })
    //       //  error.message = '(500)';
    //       break
    //     default: error.message = `(${error.response.status})!`;
    //   }
    // }
    return Promise.reject(error.response) // 
  }
)

export default axios
